<template>
    <div class="">
        <loader :is-visible="isLoading"></loader>
                        <tg-list :IsShowAction="true" ModuleName="Billing" SubModuleCode="Setup" :LegendArray="[]"
                            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
                            IdentityColumn="SETUP_ID" :listType="['List']" :ListData="ListingData" :HeaderData="Headers"
                            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
                            IsSearchFieldsOnTop="true" SearchPosition="top" :ListDataCallBackFunction="FetchData"
                            :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy"
                            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
                            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                            v-on:SortdataButtonClick="sortdata">
                            <template v-slot:slotdata="{ data }">
                                 <template v-if="data.column.COLUMN_NAME == 'SETUP_NAME'">
                                 <span class="link" @click="openBillSetup(data.row)">
                                    {{ data.row.SETUP_NAME }}
                                </span>
                                </template>
                                <template v-if="data.column.COLUMN_NAME == 'CREATED_AT'">
                                      {{ formatDate(data.row.CREATED_AT) }}
                                </template>
                                <template v-if="data.column.COLUMN_NAME == 'TEMPLATE_COUNT'">
                                    <a title="" @click="OpenTemplateList(data.row)" class="text-dark"  data-toggle-tooltip="tooltip" data-original-title="View Template"><em class="fa fa-file-text-o" aria-hidden="true"></em><span class="noti-circle noti-orange">{{ data.row.TEMPLATE_COUNT }}</span></a>
                                </template>
                            </template>
                        </tg-list>
    </div>
</template>
<script>
import  { locateBillingService } from '../../services/locateBillingService'
import Schema from './ImportFormatSchema'
export default {
    data() {
        return {
            isLoading: false,
            HeaderText: '',
            TagId:0,
            TagArr:[],
            AddTagPopup: false,
            listheaderbuttons: Schema.buttons,
            Headers: [{
                "COLUMN_NAME": "SETUP_NAME",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Setup Name',
                "DISPLAY_ORDER": 1,
                settings: {
                        isInSlot: true,
                },

            },
            {
                "COLUMN_NAME": "CREATED_BY",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Created by',
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "CREATED_AT",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Created On',
                "DISPLAY_ORDER": 3,
                 settings: {
                        isInSlot: true,
                },
            },
            {
                "COLUMN_NAME": "TEMPLATE_COUNT",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": '# of Template',
                "DISPLAY_ORDER": 4,
                 settings: {
                        isInSlot: true,
                },
            }],
            SortBy: '',
            skillStringArr: [],
            SortExp: '',
            TotalRecords: 0,
            searchCondition: '',
            PageNumber: 1,
            PageSize: 10,
            ListingData: [],
            CurrentPage: 1,
            TotalPages: 0,
            leftSearchFields: [{
                fieldName: 'Search',
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName: "SetupName",
                isSearch: true,
                value: "",
                listOptions: []
            }],
            noRecords:false,
            listActions: Schema.listActions,
        }
    },
    created: function () {
        var vm = this;
        vm.FetchData();
    },
    methods: {
          renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'AddTemplate':
                        action.isVisible = true;
                        break;
                    case 'View':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "BillSetup":
                    this.AddSetup();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },
        commonDeleteData: function (id) {
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Tag")), function () {
                    var url = `ids=${val}&tablename=TALYGEN_BE_SETUP&primaryKey=SETUP_ID`;
                    locateBillingService.DeleteItems(url).then(function (response) {
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Setup')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Setup')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        actionButtonClick: function (callbackfunction, item) {
            switch (callbackfunction) {
                case 'AddTemplate':
                    this.AddTemplate(item);
                    break;
                case 'EditRecord':
                    this.editRecord(item);
                    break;
                case 'ViewTemplate':
                    this.viewRecord(item);
                    break;
            }
        },
        AddSetup: function (item) {
            var vm =this;
             vm.$router.push({ name: 'Import' });
        },
        AddTemplate: function (item) {
            var vm =this;
             var route = '/BillingRule/Template';
            if (item.SETUP_ID) {
                route += `?setupId=${item.SETUP_ID}`;
            }
            vm.$router.push(route);
        },
        openBillSetup: function (item) {
            var vm =this;
            if(item.SETUPCOUNT==0){
                vm.ShowAlert(vm.$t('AlreadyUsedSetup'), 'warning', true, vm.$t('Alert'));
            }else{
                vm.$router.push({ name: 'SetupTemplate', params: { setupId: item.SETUP_ID, isEditable : 1, Update: true} });
            }
        },
        OpenTemplateList(item) {
            this.$router.push({ name: 'TemplateListing', params: { setupId: item.SETUP_ID } });
        },
        viewRecord(item) {
            this.$router.push({ name: 'SetupDetail', params: { setupId: item.SETUP_ID } });
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;

            if (typeof value.searchConditionJson !== 'undefined') {
                this.searchCondition = value.searchConditionJson.split('=')[1] || '';
            } else {
                this.searchCondition = "";
            }

            this.FetchData();
        },
        FetchData: async function () {
            var vm = this;
            var params = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&setupName=${vm.searchCondition}`;
            await locateBillingService.SetupListing(params).then((response) => {
                vm.ListingData=[];
                  if(response.data.result){
                    vm.ListingData=JSON.parse(response.data.result);
                }
                 vm.ListingData.forEach(item => {
                    item.isCheckBoxDisabled = false;
                    if (item.SETUPCOUNT ==0) {
                      item.isCheckBoxDisabled = false;
                    }else{
                      item.isCheckBoxDisabled = true;
                    }
                });
                if (vm.ListingData  && vm.ListingData.length > 0) {
                    vm.TotalRecords = vm.ListingData[0].TOTAL_RECORDS || vm.ListingData[0].TOTALRECORDS || 0;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                } else {
                    vm.ListingData = [];
                    vm.TotalRecords = 0;
                }
            })
        },
        formatDate(dateStr) {
            let dt = new Date(dateStr);
            let month = dt.getMonth() + 1;
            let day = dt.getDate();
            let year = dt.getFullYear();
            return `${month}/${day}/${year}`;
        }
    }
}
</script>
<style>
    .link {
    color: #007bff;
    cursor: pointer;
}
</style>
    
    